body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(to right, #3775dd, #dc97dc);
  max-width: 1440px;
  height: auto;
  margin: 0 auto;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
  color: #000;
}

.App {
  text-align: center;
  margin: 20px auto;
  max-width: 50%;
  height: auto;
  background-color: rgba(236, 231, 231, 0.6);
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(101, 101, 101, 0.5);
}

@media screen and (max-width: 425px) {
  .App{
    max-width: 90%;
  }
}

h1 {
  margin: 0;
  padding: 20px 0 20px;
}


.start-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10% auto;
    grid-gap: 10px;
    gap: 10px;
}

h1 {
    margin: 0;
    padding: 0;
}
input {
    height: 3rem;
    font-size: 15px;
    border-radius: 10px;
    border: none;
    padding: 0.375rem 0.75rem;
    margin: 5px;
    color: #212529;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(101, 101, 101, 0.5);
}

button {
    cursor: pointer;
    font-size: 15px;
    width: 7rem;
    height: 3rem;
    border-radius: 10px;
    border: none;
    margin: 5px;
    background-color: #3775dd;
    box-shadow: 0 0 5px #25426c;
}

button:hover {
    background-color: #3d6fc5;
}
.todo-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10% auto;
}

li {
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    align-items: center;
    padding: .5rem 1rem;
    border: 1px solid rgba(101, 101, 101, 0.5);
    border-radius: 10px;
    margin-bottom: .5rem;
}
img {
    width: 45px;
    height: 45px;
}

button {
    cursor: pointer;
}
.header__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
}
.header__user {
    padding: 0;
}

.header__active-toDo {
    margin: 0;
}
.edit-window {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}   

.edit-window__content {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    grid-gap: 3rem;
    gap: 3rem;
    width: 100%;
    max-width: 600px;
    max-height: calc(100vh - 40px);
    background: white;
    padding: 1rem;
    margin: 0 auto;
    text-align: left;
    overflow: hidden;
    box-shadow: 0 0 5px rgba(101, 101, 101, 0.5);
}

.edit-window__input {
    border: 1px solid rgba(101, 101, 101);
}
.list-items {
    padding: 0;
}

.buttons {
    display: flex;
    grid-gap: 5px;
    gap: 5px;
    margin-left: 2rem;

}
